import classes from "./day.module.css";
import React from "react";

const day = (props) => {
 return (
    <div className={classes.Item}>
      <h2 className={classes.Day}>{props.day}</h2>
      <div className={classes.Flex} >
        <div className={classes.Meal}>
          MIDI :
          <p>
            <span onClick={() => props.clicked(["üçï", "lunch"])}>
              üçï
            </span>
            <span onClick={() => props.clicked(["üç£", "lunch"])}>
              üç£
            </span>
            <span onClick={() => props.clicked(["ü•¶", "lunch"])}>
              ü•¶
            </span>
          </p>
        </div>
        <div className={classes.Meal}>
          SOIR :
          <p>
            <span onClick={() => props.clicked(["üçï", "dinner"])}>
              üçï
            </span>
            <span onClick={() => props.clicked(["üç£", "dinner"])}>
              üç£
            </span>
            <span onClick={() => props.clicked(["ü•¶", "dinner"])}>
              ü•¶
            </span>
          </p>
        </div>
      </div>
    </div>
  );
 }
 
export default day; 

 
    

